@mixin rich-media {
  @include colors(white-on-green);

  padding-top: $lem * 8;
  display: flex;
  flex-direction: column;

  &__headline {
    @include rich-media__headline;
  }

  .rich-media-hero {
    @include rich-media-hero;
  }

  .articles {
    @include articles($margin-right: 0);
    @include section-margin;

    .article {
      padding-left: $lem * 2.66 + $lem * 1.666;

      &:first-of-type {
        padding-left: $lem * 2.66;
      }

      &__subtitle {
        margin-top: $lem;
        margin-bottom: $lem / 2;
      }

      &__excerpt {
        max-width: 14.4em;
      }
    }
  }

  @include rich-media--landscape;
  @include rich-media--tablet-and-below;
}
