@mixin everything__hero {
  display: flex;
  justify-content: center;
  flex-basis: 100%;
  width: 100%;

  .article {
    @include article;
    @include article--centered;
    @include section-margin;

    &__excerpt {
      max-width: none;
      margin-top: $lem;
      line-height: 1;
    }
  }
}
