@mixin hero {
  @include section-padding;

  // The height of the header must be added here, since the header has position: absolute
  margin-top: $lem * 3;

  // To make room for devices
  min-height: 150vh;

  display: flex;
  flex-direction: column;
  align-items: center;

  .article {
    @include hero__article;
  }

  &__mockups {
    @include hero__mockups;
  }

  @include hero__sliders;

  &--fixed {
    @include hero--fixed;
  }
}
